.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.answerBox{
  font-size:1.75rem;
}
button{
  font-size: 1.75rem;
}
.header{
  height:15%;
  background-color: #222;
  font-size:calc(10px + 2vmin);
  color:#EEE;
  padding:1rem;
}
.mainWrapper{
  align-items: center;
  justify-content: center;
  padding:1rem;
}
.cell {
  padding:.75rem .25rem 0 .25rem;
  font-size:1.5rem;
}
.ButtonBox{
  margin: .5rem;
}
